import MainLayout from "@/components/Layouts/layout";
import ContentHeader from "@/components/Elements/content-header";
import { jobCardData } from "@/lib/models/client";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/Elements/collapsible";
import { ChevronDown } from "lucide-react";
import { Card, CardContent, CardHeader } from "@/components/Elements/Card";
import { clientInfo } from "@/lib/models/client";
import { JobCard } from "@/components/Elements/jobCard";
import { useRouter } from "next/router";
export default function Client() {
  const isClient = true;
  const router = useRouter();
  const id = router.query.id;

  return (
    <MainLayout>
      <ContentHeader title="Client Info" />
      <div className="space-y-8">
        <Card className="p-4">
          <CardHeader>
            <h1>SeerTech Systems</h1>
          </CardHeader>
          <CardContent className="bg-white p-4 mt-4">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              {Object.entries(clientInfo[Number(id) - 1]).map(
                ([key, value], index) => (
                  <div key={index} className="space-y-2">
                    <p className="font-semibold">
                      {key.charAt(0).toUpperCase() + key.slice(1)}
                    </p>
                    <p className="text-gray-500">{value}</p>
                  </div>
                )
              )}
            </div>
            <div className="flex justify-end">
              <button className="bg-[var(--theme-background)] text-white py-2 px-4 rounded-lg">
                Update
              </button>
            </div>
          </CardContent>
        </Card>

        <div className="space-y-6">
          <div className="space-y-4">
            <h2 className="text-xl font-semibold">All Branch</h2>

            <div className="space-y-2">
              {["Chennai", "Delhi", "Hyderabad", "Mumbai"].map(
                (city, index) => (
                  <Collapsible key={city}>
                    <CollapsibleTrigger className="flex items-center justify-between w-full p-4 border rounded-lg hover:bg-accent">
                      <span>{city}</span>
                      <ChevronDown className="h-4 w-4" />
                    </CollapsibleTrigger>
                    <CollapsibleContent className="mt-2">
                      {index === 0 && ( // Only render content for Chennai (index 0)
                        <div className="p-6 bg-muted rounded-lg space-y-4 bg-gray-200">
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                              <p className="text-sm">State</p>
                              <p>Chennai</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">Pin Code</p>
                              <p>811302</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Contact No</p>
                              <p>8292782487</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Email</p>
                              <p>abc@gmail.com</p>
                            </div>
                          </div>
                          <div className="space-y-2">
                            <p className="text-sm">Company Land Line</p>
                            <p>8292782487</p>
                          </div>
                        </div>
                      )}
                    </CollapsibleContent>

                    <CollapsibleContent className="mt-2">
                      {index === 1 && ( // Only render content for Delhi (index 1)
                        <div className="p-6 bg-muted rounded-lg space-y-4 bg-gray-200">
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                              <p className="text-sm">State</p>
                              <p>Delhi</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">Pin Code</p>
                              <p>110001</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Contact No</p>
                              <p>9876543210</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Email</p>
                              <p>def@gmail.com</p>
                            </div>
                          </div>
                          <div className="space-y-2">
                            <p className="text-sm">Company Land Line</p>
                            <p>9876543210</p>
                          </div>
                        </div>
                      )}
                    </CollapsibleContent>

                    <CollapsibleContent className="mt-2">
                      {index === 2 && ( // Only render content for Hyderabad (index 2)
                        <div className="p-6 bg-muted rounded-lg space-y-4 bg-gray-200">
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                              <p className="text-sm">State</p>
                              <p>Hyderabad</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">Pin Code</p>
                              <p>500001</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Contact No</p>
                              <p>8765432109</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Email</p>
                              <p>ghi@gmail.com</p>
                            </div>
                          </div>
                          <div className="space-y-2">
                            <p className="text-sm">Company Land Line</p>
                            <p>8765432109</p>
                          </div>
                        </div>
                      )}
                    </CollapsibleContent>

                    <CollapsibleContent className="mt-2">
                      {index === 3 && ( // Only render content for Mumbai (index 3)
                        <div className="p-6 bg-muted rounded-lg space-y-4 bg-gray-200">
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                              <p className="text-sm">State</p>
                              <p>Mumbai</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">Pin Code</p>
                              <p>400001</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Contact No</p>
                              <p>7654321098</p>
                            </div>
                            <div className="space-y-2">
                              <p className="text-sm">HR Email</p>
                              <p>jkl@gmail.com</p>
                            </div>
                          </div>
                          <div className="space-y-2">
                            <p className="text-sm">Company Land Line</p>
                            <p>7654321098</p>
                          </div>
                        </div>
                      )}
                    </CollapsibleContent>
                  </Collapsible>
                )
              )}
            </div>
          </div>
        </div>

        <div>
          <div className="flex items-center justify-between">
            <h3 className="text-2xl font-semibold">Active Jobs</h3>
            <button className="bg-blue-500 text-white py-2 px-4 rounded-lg">
              Add New Job
            </button>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
            <JobCard {...jobCardData} isClient={isClient} />
            <JobCard {...jobCardData} isClient={isClient} />
          </div>
        </div>
      </div>
    </MainLayout>
  );
}
